<template>
<div class="kt-aside  kt-aside--fixed  kt-grid__item kt-grid kt-grid--desktop kt-grid--hor-desktop" id="kt_aside" v-show="visible">
	<div class="kt-aside__brand kt-grid__item " id="kt_aside_brand">
		<div class="kt-aside__brand-logo">
			<a href="/home" class="homer-page-linker" id="logobrandlinker">
				<img id="imagelogobrand" alt="Logo" src="../../assets/img/app_logo_title.png" />
			</a>
		</div>
		<div class="kt-adside_brand-logo-short">
			<a href="/home" class="homer-page-linker" id="logoshortlinker">
				<img id="imagelogobrandshort" alt="Logo" src="../../assets/img/app_logo_short.png" />
			</a>
		</div>
		<div class="kt-aside__brand-tools">
			<button class="kt-aside__brand-aside-toggler" id="kt_aside_toggler">
				<span><i class="la la-angle-double-left"></i></span>
				<span><i class="la la-angle-double-right"></i></span>
			</button>
		</div>
	</div>
	<div class="kt-aside-menu-wrapper kt-grid__item kt-grid__item--fluid" id="kt_aside_menu_wrapper">
		<div id="kt_aside_menu" class="kt-aside-menu " data-ktmenu-vertical="1" data-ktmenu-scroll="1" data-ktmenu-dropdown-timeout="500">
			<ul class="kt-menu__nav py-0">
				<li class="kt-menu__item " aria-haspopup="true">
					<span id="menulinkspaner" class="kt-menu__link ">
						<span class="kt-menu__link-text">
							<div id="sidebarheader" class="sidebar-header">
								<div class="input-group-search has-search">
									<span id="searchspan" class="fa fa-search form-control-search" aria-hidden="true"></span>
									<input type="text" id="sidemenusearchtext" class="form-control2 form-control-input-search input-md" placeholder="Search" />
								</div>
							</div>
						</span>
					</span>
				</li>
				<li id="sidebar-home" class="kt-menu__item  kt-menu__item--active" aria-haspopup="true">
					<a href="javascript:void(0)" @click="menuSelected('home')" class="kt-menu__link home-page-linker" id="homepagelinker">
						<span class="kt-menu__link-icon">
							<i class="fa fa-home"></i>
						</span>
						<span class="kt-menu__link-text">{{ labels.homepage_title }}</span>
					</a>
				</li>
			</ul>
			<div ref="sidebarlayer" id="sidebarlayer">
      </div>
			<div ref="sidebarlayertree" id="sidebarlayertree">
        <MetroSiderMenu ref="siderMenu" :lang="accessor.lang" :menus="menus" :metas="menuMetas" @item-menu-selected="itemMenuSelected" @group-menu-selected="groupMenuSelected" />
			</div>
			<div class="spanbuildversion" id="spanbuildversion">
				Build : {{ buildVersion }}
			</div>
		</div>
	</div>
</div>
</template>
<script>
import $ from "jquery";
import { ref } from 'vue';
import { getApiUrl, DEFAULT_CONTENT_TYPE, getRequestID, getMetaInfo } from "@willsofts/will-app";
import { serializeParameters } from "@willsofts/will-app";
import { openPage } from "../../assets/js/login.util.js";
import { accessor } from "../../assets/js/accessor.js";
import { favorite } from "../../assets/js/favorite.js";
import { progmap, menulists, loaded } from "../../assets/js/sider.js";
import MetroSiderMenu from "./MetroSiderMenu.vue";
const buildVersion = process.env.VUE_APP_BUILD_VERSION;
const menuData = {sidemap: {}, sidelist: {}};
const metaData = {MENU_TREE: true};

export default {
  components: { MetroSiderMenu },
  props: {
    labels: Object,    
    visible: {
      type: [String,Boolean],
      default: true,
    },
  },
  setup() {
    const menus = ref({...menuData});
    const menuItems = ref({...menuData});
    const menuMetas = ref({...metaData});
    const searchingVisible = ref(false);
    const menuFlip = ref(true);
    return { buildVersion, accessor, favorite, menus, menuItems, menuMetas, searchingVisible, menuFlip, progmap, menulists, loaded };
  },
  mounted() {
    this.$nextTick(() => {
      this.initialize();
    });
  },
  methods: {
    reset() {
      console.log("SiderBar.vue: reset ...");
      this.menuItems = {...menuData};
      this.menuMetas = {...metaData};
      this.progmap = {};
    },
    initialize() {
      console.log("SiderBar: initialize ...");
			$("#sidemenusearchtext").autocomplete({
        delay: 500, //delay keystroke to be search from server
				select : (event, ui) => {
					console.log("side menu search selected",JSON.stringify(ui.item));
          $(ui.item["element"]).trigger("click");
          //let tree = {caption: this.$refs.siderMenu.getDisplayProgramName(ui.item.element), treepath: "Search Program#CUT#"};
          //this.itemMenuSelected(ui.item.element,tree);
				}
			});
    },
    menuSelected(menu) {
      this.$emit("menu-selected", menu);
    },
    displaySideBarMenu() {
      this.menuFlip = !this.menuFlip;
      this.searchingVisible = !this.searchingVisible;
    },
    collapseSideBarMenu() {
      this.searchingVisible = false;
      this.menuFlip = true;
    },
    setting(callback) {
      console.log("setting",this.accessor);
      this.loadSideBarMenu(callback);
    },
    show() {
      this.searchingVisible = false;
      this.menuFlip = true;
    },
    changeLanguage(lang) {
      console.log("SiderBar.vue: language changed",lang);
      this.changeSiderMenuLanguage(lang);
      this.buildSearching();
    },
    initSearching(lang) {
      let jsAry = Object.values(this.menuItems.sidelist).flat().map((item) => { return {
        label : lang=="EN"?item.progname:item.prognameth, element : item
      }});
      return jsAry;
    },
    initMenuItems(dataset) {
      if(!dataset) return;
      let sidelist = {};
      let progmap = {};
      for(let k in dataset.sidelist) {
        let ary = dataset.sidelist[k];
        for(let p of ary) {
          if(!progmap[p.programid]) {
            progmap[p.programid] = p;
          }
        }
      }
      //try to remove plugin program (progtype=I)
      for(let p in dataset.sidelist) {
        let ary = dataset.sidelist[p];
        let items = ary.filter((item) => item.progtype!='I');
        if(items.length == 0) {
          let it = dataset.sidemap[p];
          if(it) items = [it];
        }
        sidelist[p] = items;
      }
      for(let g in dataset.sidemap) {
        let item = dataset.sidemap[g];
        let jsonmenu = undefined;
        if(item.menutext && item.menutext.trim().length > 0) {
            try { jsonmenu = JSON.parse(item.menutext); } catch(ex) { console.error(ex); }
        }
        item.jsonmenu = jsonmenu;
      }
      this.progmap = progmap;
      this.menuItems = {sidemap: dataset.sidemap, sidelist: sidelist };
      console.log("initMenuItems: menu items",this.menuItems);
    },
    itemMenuSelected(item,tree) {
      if(!tree) {
        tree = {caption: this.$refs.siderMenu.getDisplayProgramName(item), treepath: this.$refs.siderMenu.getTreePath(item)};
      }
      openPage(item,this.accessor,this.favorite,tree);
    },
    groupMenuSelected(group) {
      console.log("grup-menu-selected",group);
    },
    loadSideBarMenu(callback) {
      this.loaded.reset();
      console.log("loadSideBarMenu: accessor",this.accessor,"callback",callback);
      let access_user = this.accessor.info?.userid;
      if(!access_user || access_user.trim().length==0) return;
      let requestid = getRequestID();
      let language = this.accessor.lang;
      let params = { userid: access_user, language: language };
      let formdata = serializeParameters(params);
      $.ajax({
        url: getApiUrl()+"/api/menuside/html",
        data: formdata.jsondata,
        headers : { ...formdata.headers, "x-request-id": requestid },
        type: "POST",
        dataType: "html",
        contentType: DEFAULT_CONTENT_TYPE,
        success: (data) => { 
          $("#sidebarlayer").html(data); 
          this.bindingHtmlMenus();
          this.loaded.htmlloaded = true;
          if(callback) callback(this.loaded.success());
        },
      });
      this.loadSideBarMenuItem(callback);
		},
    loadSideBarMenuItem(callback) {
      console.log("loadSideBarMenuItem: accessor",this.accessor);
      let access_user = this.accessor.info?.userid;
      if(!access_user || access_user.trim().length==0) return;
      let requestid = getRequestID();
      let language = this.accessor.lang;
      let params = { userid: access_user, language: language };
      let formdata = serializeParameters(params);
      $.ajax({
        url: getApiUrl()+"/api/menu/side",
        data: formdata.jsondata,
        headers : { ...formdata.headers, "x-request-id": requestid },
        type: "POST",
        dataType: "json",
        contentType: DEFAULT_CONTENT_TYPE,
        success: (data) => { 
          console.log("loadSideBarMenuItem: success",data);
          if(data.body?.dataset) {
            this.menuMetas = {...metaData, ...data.body?.meta};
            this.initMenuItems(data.body.dataset);   
            this.menulists = this.initSearching(language);
            this.loaded.itemloaded = true;
            if(callback) callback(this.loaded.success(),this.menulists);
          }
        },
      });
		},
    bindingHtmlMenus() {
      this.buildSearching();
      $("#sidebarlayer").find("a.fa-link-menu-item").each((index,element) => {
        let $e = $(element);
        $e.on("click",() => {
          let pid = $e.attr("data-pid");
          let item = this.progmap[pid];
          console.log("menu item click: ",pid,":",item);
          if(item) {            
            let tree = { caption : this.$refs.siderMenu.getDisplayProgramName(item), treepath: $e.attr("data-tree") };
            this.itemMenuSelected(item,tree);
          }
        });
      });
    },
    buildSearching() {
      let jsAry = $.map($("#sidebarlayer").find("a.menu-desktop").toArray(),function(element) { 
        let e = $(element);
        return { 
          label : e.text().trim(),
          element : e
        };
      });
      $("#sidemenusearchtext").autocomplete("option","source",jsAry);
      return jsAry;
    },
    changeSiderMenuLanguage(lang) {
      lang = lang.toLowerCase();
      $("#sidebarlayer").find("span.span-menu-label").each(function(index,element) {
        let e = $(element);
        let text = e.attr("data-"+lang);
        if(text) e.html(text);
			});
    },
    openFirstPage(success,menulists) {
      let page = this.accessor.info?.firstpage || "worklist";
      console.log("openFirstPage:",page,"success",success);
      if(!success) return false;
      if(!menulists) menulists = this.menulists;
      if(menulists && (page && page.trim().length > 0)) {
        let prog = menulists.find((item) => item.element.programid == page );
        console.log("openFirstPage: prog",prog);
        if(prog) {
          let treepath = null;
          let pages = $("a[data-pid="+page+"]",$("#sidebarlayer"));
          if(pages && pages.length>0) {
            treepath = pages.eq(0).attr("data-tree");
          }
          let tree = { caption : this.$refs.siderMenu.getDisplayProgramName(prog.element), treepath: treepath };
          openPage(prog.element,this.accessor,this.favorite,tree);
          if(!(String(getMetaInfo().DISPLAY_WORKLIST_SUBHEADER)=="true")) {
            $("#kt_subheader").addClass("dp-none");
            $("#kt_content").addClass("no-subheader");          
          }
          return true;
        }
      }
      return false;
    },
  },
};
</script>
