<template>
    <div id="header_cover_layer" class="kt-head kt-head--skin-dark">
        <h3 class="kt-head__title font-size-custom-h2">
            {{ labels.favorite_title }}
            <span class="kt-space-15"></span>
            <div id="favornewitemlayer" v-show="newFavorVisible">
                <a href="javascript:void(0)" @click.stop="cancelNewFavorClick" id="favorcancelitem" class="btn btn-danger btn-icon" title="Close New Favorite"><em class="fa fa-times-circle"></em></a>
                <select id="favorprogitem" class="form-control" v-model="favorProg" @click.stop>
                    <option v-for="(item, index) in favorite.proglists" :key="index" :value="item.programid">{{getDisplayProgramName(item)}}</option>
                </select>
                <a href="javascript:void(0)" @click.stop="addFavorItemClick" id="favornewitem" class="btn btn-success btn-icon" title="Add New Favorite">
                    <em class="fa fa-plus"></em>
                </a>
            </div>
        </h3>
    </div>
    <div id="favorbarmenu" class="kt-grid-nav kt-grid-nav--skin-light">
        <template v-if="!isFavorEmpty()">
        <div class="kt-grid-nav__row" v-for="jdx in 3" :key="jdx">
            <template v-for="idx in 3" :key="(jdx - 1) * 3 + (idx - 1)">
                <a v-if="isNewFavorByIndex((jdx - 1) * 3 + (idx - 1))" href="javascript:void(0)" @click.stop="newFavorItemClickByIndex((jdx - 1) * 3 + (idx - 1))" class="kt-grid-nav__item tile fa-box-title fav-blank" title="New Favorite" :seqno="getFavorSeqnoByIndex((jdx - 1) * 3 + (idx - 1))">
                    <span class="kt-grid-nav__icon">
                        <img class="fa fa-app-image img-favor-icon" alt=""/>
                    </span>
                    <span class="kt-grid-nav__title font-size-custom-h-14">{{ labels.newfavorite_title }}</span>
                </a>
                <a v-else href="javascript:void(0)" @click="openFavorItemClickByIndex((jdx - 1) * 3 + (idx - 1))" class="kt-grid-nav__item tile fa-box-title fav-app" :seqno="getFavorSeqnoByIndex((jdx - 1) * 3 + (idx - 1))">
                    <span class="kt-grid-nav__icon">
                        <img class="fa fa-app-image" :src="getFavorIconByIndex((jdx - 1) * 3 + (idx - 1))" alt=""/>
                        <span class="kt-grid-nav__title font-size-custom-h-14">{{ getDisplayFavorNameByIndex((jdx - 1) * 3 + (idx - 1)) }}</span>
                    </span>
                    <span class='todo-span'>
                        <ul class="todo"><li @click.stop="deleteFavorItemClickByIndex((jdx - 1) * 3 + (idx - 1))"><img class='img-delete-icon' title="Delete" width="25px" height="25px"/></li></ul>
                    </span>
                </a>
            </template>
        </div>
        </template>
    </div>
</template>
<style>
a.tile.fa-box-title.fav-app .todo-span > .todo { display: none; }
a.tile.fa-box-title.fav-app:hover .todo-span > .todo { display: block; }
</style>
<script>
import $ from "jquery";
import { ref } from 'vue';
import { getApiUrl, getImgUrl, DEFAULT_CONTENT_TYPE, getRequestID } from "@willsofts/will-app";
import { submitFailure, serializeParameters } from "@willsofts/will-app";
import { openPage } from "../../assets/js/login.util.js";
import { accessor } from "../../assets/js/accessor.js";
import { favorite } from "../../assets/js/favorite.js";

export default {
  props: {
    labels: Object,
    visible: {
      type: [String,Boolean],
      default: true,
    },
  },
  setup() {
    const newFavorVisible = ref(false);
    const favorProg = ref(null);
    const currentFavor = ref(null);
    return { accessor, favorite, newFavorVisible, favorProg, currentFavor };
  },
  methods: {
    reset() {
        console.log("FavorMenu.vue: reset ...");
        this.favorite.reset();
    },
    isFavorEmpty() {
        return this.favorite.favorlists.length == 0;
    },
    getFavorItem(index) {
        return this.favorite.favorlists[index];
    },
    getFavorIcon(item) {
        return item.iconfile && item.iconfile.trim().length > 0 ? getImgUrl()+"/img/apps/"+item.iconfile : this.getDefaultFavorIcon(item);
    },
    getFavorSeqno(item,index) {
        return item.seqno ? item.seqno : index;
    },
    getDefaultFavorIcon(item) {
        return item.type=='new' ? getImgUrl()+"/img/apps/favorite.png" : getImgUrl()+"/img/apps/application.png";
    },
    getDisplayFavorName(item) {
        return this.accessor.lang === 'TH' ? item.shortnameth : item.shortname;
    },
    getDisplayProgramName(item) {
        return this.accessor.lang === 'TH' ? item.shortnameth : item.shortname;
    },
    setting() {
        console.log("FavorMenu: setting, accessor",this.accessor);
        this.loadFavorMenu();
        this.loadProgramItems();
    },
    loadFavorMenu() {
        console.log("loadFavorMenu: accessor",this.accessor);
        let info = this.accessor.info;
        let access_user = info?.userid;
        if(!access_user || access_user.trim().length==0) return;
        let requestid = getRequestID();
        let language = this.accessor.lang;
        let params = { userid: access_user, language: language };
        let formdata = serializeParameters(params);
        $.ajax({
            url: getApiUrl()+"/api/menu/favor",
            data: formdata.jsondata,
            headers : { ...formdata.headers, "x-request-id": requestid },
            type: "POST",
            dataType: "json",
            contentType: DEFAULT_CONTENT_TYPE,
            success: (data) => { 
                console.log("loadFavorMenu: success",data);
                let dataset = data.body?.dataset;
                if(dataset) {
                    let rows = dataset.rows;
                    if(rows) {
                        for(let idx = rows.length + 1; idx <= 9; idx++) {
                            rows.push({type:"new", seqno: idx});
                        }
                        this.favorite.setFavorLists(rows);
                    }
                }
            }
        });
    },
    loadProgramItems() {
        console.log("loadProgramItems: accessor",this.accessor);
        let requestid = getRequestID();
        let access_user = this.accessor.info?.userid;
        if(!access_user || access_user.trim().length==0) return;
        let formdata = serializeParameters({ userid: access_user });
        $.ajax({
            url: getApiUrl()+"/api/menu/favorprog",
            data: formdata.jsondata,
            headers : { ...formdata.headers, "x-request-id": requestid },
            type: "POST",
            dataType: "json",
            contentType: DEFAULT_CONTENT_TYPE,
            success: (data) => { 
                console.log("loadProgramItems: success",data);
                if(data.body?.rows) {
                    this.favorite.setProgLists(data.body.rows);
                }
            },
        });	
    },
    cancelNewFavorClick() {
        this.newFavorVisible = false;
    },
    addFavorItemClick() {
        console.log("addFavorItem: current favor",this.currentFavor);
        console.log("addFavorItem: favor prog",this.favorProg);
        if(this.currentFavor && this.favorProg) {
            let prog = this.favorite.proglists.find((item) => item.programid == this.favorProg);
            console.log("addFavorItem: prog item",prog);
            if(!prog) return;
            let fs_user = this.accessor.info?.userid;
            let fs_prog = prog.programid;
            let fs_seqno = this.currentFavor.seqno;
            let params = { userid: fs_user, programid: fs_prog, seqno: fs_seqno };
            let formdata = serializeParameters(params);
            $.ajax({
                url: getApiUrl()+"/api/menu/insert",
                data: formdata.jsondata,
                headers : formdata.headers,
                type: "POST",
                dataType: "html",
                contentType: DEFAULT_CONTENT_TYPE,
                error : function(transport,status,errorThrown) { 
                    submitFailure(transport,status,errorThrown,false);  
                },
                success: () => { 
                    Object.assign(this.currentFavor,prog);
                    this.currentFavor.type = "add";
                    this.newFavorVisible = false;
                },
            });	
        }
    },
    newFavorItemClick(item) {
        console.log("newFavorItem: item",item);
        this.currentFavor = item;
        this.newFavorVisible = true;
    },
    openFavorItemClick(item) {
        console.log("openFavorItem: item",item);
        openPage(item,this.accessor,this.favorite,{caption: this.getDisplayProgramName(item), treepath: "Shortcut Program#CUT#"});
    },
    deleteFavorItemClick(item,index) {
        console.log("deleteFavorItem: item",item);
        let fs_user = this.accessor.info?.userid;
        let fs_seqno = item.seqno ? item.seqno : index;
        let params = { userid: fs_user, programid: item.programid, seqno: fs_seqno };
        let formdata = serializeParameters(params);
		$.ajax({
			url: getApiUrl()+"/api/menu/remove",
            data: formdata.jsondata,
            headers : formdata.headers,
			type: "POST",
			dataType: "html",
			contentType: DEFAULT_CONTENT_TYPE,
			error : function(transport,status,errorThrown) { 
				submitFailure(transport,status,errorThrown,false);  
			},
            success: () => { 
                item.type = "new";
            },
		});
    },
    newFavorItemClickByIndex(index) {
        this.newFavorItemClick(this.getFavorItem(index));
    },
    getFavorSeqnoByIndex(index) {
        return this.getFavorSeqno(this.getFavorItem(index),index);
    },
    openFavorItemClickByIndex(index) {
        this.openFavorItemClick(this.getFavorItem(index));
    },
    getFavorIconByIndex(index) {
        return this.getFavorIcon(this.getFavorItem(index));
    },
    deleteFavorItemClickByIndex(index) {
        this.deleteFavorItemClick(this.getFavorItem(index),index);
    },
    getDisplayFavorNameByIndex(index) {
        return this.getDisplayFavorName(this.getFavorItem(index));
    },
    isNewFavorByIndex(index) {
        let item = this.getFavorItem(index);
        return item?.type == 'new';
    },
  },
};
</script>
