<!-- App.vue -->
<template>
  <div id="fswaitlayer" class="fa fa-spinner fa-spin"></div>
  <div id="mainlayer" ref="mainlayer" v-show="isShowing == true">
    <MetroHeaderBarMobile ref="headerBarMobile" :visible="menuVisible" :labels="labels" />
    <div id="aside_header" class="kt-grid kt-grid--hor kt-grid--root" v-show="menuVisible">
      <div class="kt-grid__item kt-grid__item--fluid kt-grid kt-grid--ver kt-page">
        <MetroSiderBar ref="siderBar" :visible="menuVisible" :labels="labels" @menu-selected="menuSelected" />
        <div class="kt-grid__item kt-grid__item--fluid kt-grid kt-grid--hor kt-wrapper" id="kt_wrapper">
          <MetroHeaderBar ref="headerBar" :visible="menuVisible" :labels="labels" @language-changed="changeLanguage" @menu-selected="menuSelected" @open-chat="openChat" :chatEnable="chatEnable" :notifyEnable="notifyEnable" :languageEnable="languageEnable" />
          <MetroSubHeaderBar ref="subHeaderBar" :visible="menuVisible" :labels="labels" />
          <div class="kt-content  kt-grid__item kt-grid__item--fluid kt-grid kt-grid--hor mt-3 pt-5" id="kt_content">
            <WorkerFrame ref="workerFrame" :visible="workingVisible" :labels="labels" />
          </div>
        </div>
      </div>
    </div>
    <div id="nn_alert_container" class="nn-alert-container first-push"></div>
    <div id="kt_alert_container" class="kt-alert-continer first-push"></div>
    <LoginForm ref="loginForm" :visible="loginVisible" :labels="labels" :version="programVersion" @success="loginSuccess" @forgot="forgotPassword" />    
		<div id="fschatinglayer" class="chating-class chating-control-class">
			<iframe id="chatingframe" name="chatingframe" width="100%" class="chating-frame" title="Chating"></iframe>
		</div>
    <div id="fsnotifylayer" class="notify-class notify-control-class">
      <iframe id="notifyframe" name="notifyframe" width="100%" class="notify-frame" title="Notify"></iframe>
    </div>  
  </div>
  <div id="forcelayer" ref="forcelayer" v-show="isShowing == false">
    <keep-alive>
      <component :is="currentForcePage" ref="forceComponent" :labels="labels" @activated="componentActivated" @success="processSuccess" />
    </keep-alive>
  </div>
</template>
<script>
import $ from "jquery";
import { ref } from 'vue';
import { startApplication }  from '@willsofts/will-app';
import { getLabelModel, loadAndMergeLabel, getMetaInfo, getAccessorToken, getBaseUrl, submitWindow, getDefaultLanguage } from "@willsofts/will-app";
import { refreshScreen, logOut, validAccessToken } from "./assets/js/login.util.js";
import { removeAccessorInfo } from "@willsofts/will-app";
import { accessor } from "./assets/js/accessor.js";
import { favorite } from "./assets/js/favorite.js";
import { isSSOSignedIn, doSSOLogout } from "./assets/js/auth.js";
import { startReceiveBroadcast, stopReceiveBroadcast } from "./assets/js/bc.js";
import MetroSiderBar from "./components/menu/MetroSiderBar.vue";
import MetroHeaderBar from "./components/menu/MetroHeaderBar.vue";
import MetroHeaderBarMobile from "./components/menu/MetroHeaderBarMobile.vue";
import MetroSubHeaderBar from "./components/menu/MetroSubHeaderBar.vue";
import LoginForm from "./components/form/LoginForm.vue";
import WorkerFrame from "./components/WorkerFrame.vue";
import BlankForm from "./components/form/BlankForm.vue";
import ChangeForm from "./components/form/ChangeForm.vue";
import ForgotForm from "./components/form/ForgotForm.vue";
import FactorForm from "./components/form/FactorForm.vue";

const programVersion = process.env.VUE_APP_BUILD_VERSION;

export default {
  components: {
    MetroSiderBar, MetroHeaderBar, MetroHeaderBarMobile, MetroSubHeaderBar, LoginForm, WorkerFrame, BlankForm, ChangeForm, ForgotForm, FactorForm
  },
  setup() {
    let labels = ref(getLabelModel());
    let isShowing = ref(true);
    let loginVisible = ref(false);
    let menuVisible = ref(false);
    let workingVisible = ref(false);
    let chatEnable = ref(false);
    let notifyEnable = ref(false);
    let languageEnable = ref(true);
    let currentForcePage = ref("BlankForm");
    let mode = ref("");
    return { programVersion, labels, accessor, favorite, isShowing, loginVisible, menuVisible, workingVisible, currentForcePage, mode, chatEnable, notifyEnable, languageEnable };
  },
  mounted() {
    console.log("App: on mounted ...");
    this.$nextTick(() => {
      startApplication("index");
			validAccessToken((valid,json) => {
				console.log("valid :",valid,", json :",json);
				if(!valid) {
					removeAccessorInfo();
          this.loginVisible = true;
          setTimeout(() => { this.$refs.loginForm.focus(); },5);          
				} else {
					this.verifyAfterLogin(json.body);
				}
			});
      /**
       * This try to load label and message code from server (api label & msgcode)
       * in case of server except authorize label & msgcode service 
       * then define default.js with META_INFO.ALLOW_LOAD_LABEL = true too       
       * in the other way this will load label after login success
       * */ 
      if(String(getMetaInfo().ALLOW_LOAD_LABEL)=="true") {
        this.loadLabel();
      }
    });
  },
  methods: {
    changeLanguage(lang) {
      let labelModel = getLabelModel(lang);
      this.labels = labelModel;
      this.$refs.siderBar.changeLanguage(lang);
    },
    loadLabel() {
      loadAndMergeLabel("vindex", (success) => {
        if (success) {
          this.changeLanguage(getDefaultLanguage());
        }
      });
    },
    verifyAfterLogin(body) {
      console.log("verifyAfterLogin: body",body);
      this.setAccessInfo(body);
      if(body?.factorverify && body?.factorid!="" && (body?.factorcode==undefined || body?.factorcode=="")) {
        console.log("two factor ...");
        this.isShowing = false;
        this.mode = "factor";
        this.currentForcePage = FactorForm;
      } else {
        this.verifyForcePage(body);
      }
      this.loadLabel();
    },
    verifyForcePage(body) {
      if(body?.changeflag=="1") {
          console.log("force change password ...");
          this.isShowing = false;
          this.mode = "force";
          this.currentForcePage = ChangeForm;
        } else if(body?.expireflag=="1") {
          console.log("password expired ...");
          this.isShowing = false;
          this.mode = "expire";
          this.currentForcePage = ChangeForm;
        } else {
          this.displayMenu();
          startReceiveBroadcast();
        }
    },
    setAccessInfo(info) {
      this.accessor.setInfo(info);
      let lang = this.accessor.info?.langcode || getDefaultLanguage();      
      this.$refs.headerBar.changeLanguage(lang);
      this.$refs.siderBar.changeLanguage(lang);
    },
    loginSuccess(info) {
      console.log("login success: info",info);
      this.verifyAfterLogin(info);
    },
    displayMenu() {
      this.resetBody();
      this.mode = "";
      this.currentForcePage = BlankForm;
      this.isShowing = true;
      this.loginVisible = false;
      this.menuVisible = true;
      this.$refs.headerBar.setting();
      this.$refs.siderBar.setting((success,menulists) => { this.loadFirstPage(success,menulists); });
      //this.$refs.workerFrame.setting();
      this.enableControls();
      refreshScreen();
      this.startNotify();
      this.startChating();
    },
    enableControls() {
      this.chatEnable = String(getMetaInfo()?.chat)=="true";
      this.notifyEnable = String(getMetaInfo()?.notify)=="true";
      this.languageEnable = !(String(getMetaInfo()?.language)=="false");
    },
    menuSelected(menu) {
      console.log("App.vue: menu selected:",menu);
      if("logout"==menu) { this.goLogOut(); }
      else if("home"==menu) { this.goHome(); }
      else if("intro"==menu) { this.$refs.workerFrame.hideWorkerMenu(); }
      else if("profile"==menu) { this.$refs.workerFrame.showProfile(); }
      else if("changepassword"==menu) { this.$refs.workerFrame.showChangePassword(); }
    },
    goHome() {
      this.loadFirstPage();
    },
    openWorkingPage() {
      this.workingVisible = false;
      this.$refs.headerBar.showLanguage();
      this.$refs.workerFrame.showWorkerMenu();
    },
    goLogOut() {
      $(window.document.body).addClass("body-login");
      logOut({...this.accessor.info});
      this.$refs.workerFrame.reset();
      this.$refs.headerBar.reset();
      this.$refs.siderBar.reset();
      this.$refs.loginForm.reset();
      this.$refs.loginForm.focus();
      this.loginVisible = true;
      this.menuVisible = false;
      this.accessor.reset();
      this.stopNotify();
      this.stopChating();
      if(isSSOSignedIn()) {
        doSSOLogout();
      }
      stopReceiveBroadcast();
    },
    goLogIn() {
      $(window.document.body).addClass("body-login");
      this.isShowing = true;
      this.mode = "";
      this.currentForcePage = BlankForm;
    },
    openFirstPage(success,menulists) {
      return this.$refs.siderBar.openFirstPage(success,menulists);
    },
    loadFirstPage(success=true,menulists) {
      if(!success) return;
      let found = this.openFirstPage(success,menulists);
      if(!found) this.openWorkingPage();
    },
    hideMenu() {
      this.$refs.headerBar.collapseSideBar();
    },
    resetBody() {
      $(window.document.body).removeClass("body-login");
    },
    componentActivated(name) {
      console.log("component activated: ",name);
      if("changepassword"==name) {
        this.resetBody();
        this.$refs.forceComponent.display(this.mode);
      } else if("forgot"==name) {
        this.resetBody();
        this.$refs.forceComponent.display(this.mode);
      } else if("factor"==name) {
        this.resetBody();
        this.$refs.forceComponent.display(this.mode);
      }
    },
    processSuccess(action,info) {
      console.log("processSuccess: action",action,", info",info);
      if("changepassword"==action) {
        this.displayMenu();
      } else if("factor"==action) {
        this.verifyForcePage(this.accessor.info);
      }
    },
    forgotPassword() {
      console.log("forgot password click ...");
      this.isShowing = false;
      this.mode = "forgot";
      this.currentForcePage = ForgotForm;
    },
    openChat() {
      if($("#fschatinglayer").is(":visible")) {
        $("#fschatinglayer").hide();
      } else {
        $("#fschatinglayer").show();
      }
    },
		openChating() {
			let authtoken = getAccessorToken();
			let appurl = getBaseUrl()+"/gui/chat";
			submitWindow({
				method: "POST",
				url : appurl,
				windowName: "chatingframe",
				params: "seed="+Math.random()+"&authtoken="+authtoken+"&language="+getDefaultLanguage()
			});	
		},
    startChating() {
      if(this.chatEnable) {
        this.openChating();
      }
    },
    stopChating() {
      if(this.chatEnable) {
        $("#fschatinglayer").hide();
        window.open(getBaseUrl()+"/blank.html","chatingframe");						
      }
		},
		openNotify() {
			let authtoken = getAccessorToken();
			let appurl = getBaseUrl()+"/gui/notify";
			submitWindow({
				method: "POST",
				url : appurl,
				windowName: "notifyframe",
				params: "seed="+Math.random()+"&authtoken="+authtoken+"&language="+getDefaultLanguage()
			});	
		},
    startNotify() {
      if(this.notifyEnable) {
        this.openNotify();
      }
    },
    stopNotify() {
      if(this.notifyEnable) {
				$("#nn_alert_container").empty();
				$("#kt_alert_container").empty();
        window.open(getBaseUrl()+"/blank.html","notifyframe");						
      }
		},
  }
};
</script>