import $ from "jquery"
import io from "socket.io-client";
import { getChatUrl, getMetaInfo, sendMessageToFrame } from "@willsofts/will-app";
import { openApp } from "./login.util";

let socket = null;
export function startReceiveBroadcast(chat_url) {
    if(!chat_url) chat_url = getChatUrl();
    console.log("start receive bc: ",chat_url);
    if(chat_url && chat_url.trim().length > 0) {
        socket = io(chat_url);
        socket.on('broadcast-message', function(msg) {
            console.log("broadcast-message:",msg);
            if(msg.type == "bc") {
                let div = $("<div class='bc-layer'></div>");
                let link = $("<a href=\"javascript:void(0)\" class=\"bc-close\" aria-label=\"close\"></a>").html("<em class='fa fa-close'></em>");
                link.on("click",() => { div.remove(); });
                let span = $("<span></span>").html(msg.message);
                let body = $("body");
                let isz = body.find("div.bc-layer").length;
                if(isz > 0 && isz < 10) {
                    let bottom = isz * 50;
                    div.css({bottom: bottom});
                }
                div.append(link).append(span).appendTo(body);
            } else if(msg.type == "app" && msg.app) {
                //try support open app directly
                //app info = { programid: "?", url: "?", parameters: "?", progpath: "?" }
                let div = $("<div class='bc-layer'></div>");
                let link = $("<a href=\"javascript:void(0)\" class=\"bc-close\" aria-label=\"close\"></a>").html("<em class='fa fa-close'></em>");
                link.on("click",() => { div.remove(); });
                let alink = $("<a href=\"javascript:void(0)\" class=\"app-text\"></a>").html(msg.message);
                alink.on("click",() => { openApp(msg.app); link.trigger("click"); });
                let span = $("<span></span>").append(alink);
                let body = $("body");
                let isz = body.find("div.bc-layer").length;
                if(isz > 0 && isz < 10) {
                    let bottom = isz * 50;
                    div.css({bottom: bottom});
                }
                div.append(link).append(span).appendTo(body);
                if(!(String(getMetaInfo().BC_AUTO_HIDE)=="false")) {
                    let interval = getMetaInfo().BC_AUTO_HIDE_INTERVAL || 3000;
                    setTimeout(function() { div.hide(1000, function() { div.remove(); }); },interval);
                }
            }
        });
    }
}
export function stopReceiveBroadcast() {
    if(socket) socket.disconnect();
}
export function openChatRoom(roomname,callback) {
    console.log("bc: openChatRoom",roomname);
    if(roomname && roomname.trim().length > 0) {
        let chatingframe = document.getElementById("chatingframe");
        let chatingwindow = chatingframe.contentWindow;
        if(chatingwindow) {
            try { 
                chatingwindow.openChatRoom(roomname);
                $("#fschatinglayer").show();
                if(callback) callback();
            } catch(ex) { console.error(ex); }
        }
    }
}
export function createChatRoom(roomname,callback) {
    console.log("bc: createChatRoom",roomname);
    if(roomname && roomname.trim().length > 0) {
        let chatingframe = document.getElementById("chatingframe");
        let chatingwindow = chatingframe.contentWindow;
        if(chatingwindow) {
            try { 
                chatingwindow.createChatRoom(roomname,callback);
            } catch(ex) { console.error(ex); }
        }
    }
}
export function handleIncommingMessage(payload) {
    if(payload.archetype=="willsofts" && payload.type=="chat") {
        if(payload.method=="openChatRoom") {
            openChatRoom(payload.roomname,() => {
                if(payload.callback) {
                    sendMessageToFrame({...payload});
                }
            });
        } else if(payload.method=="createChatRoom") {
            createChatRoom(payload.roomname,() => {
                if(payload.callback) {
                    sendMessageToFrame({...payload});
                }
            });
        }
    }
}
