<template>
  <div id="fsworkinglayer" class="working-class working-control-class">
    <div ref="pagecontainer" id="pagecontainer" class="pt-pager">
      <keep-alive>
        <component :is="currentComponent" :visible="componentVisible" :labels="labels" ref="viewComponent" @activated="componentActivated" @success="processSuccess" visibleHeader="false" />
      </keep-alive>
    </div> 
    <iframe id="workingframe" name="workingframe" width="100%" class="working-frame" title="Working" v-show="workingVisible"></iframe>
  </div> 
</template>
<script>
import $ from "jquery";
import { ref } from 'vue';
import { startWaiting, stopWaiting, confirmDialogBox, getMetaInfo }  from '@willsofts/will-app';
import { alertDialog, confirmDialog, alertbox, confirmbox, alertmsg, confirmmsg, successbox, warningbox }  from '@willsofts/will-app';
import { refreshScreen, hideWorkSpace, showSubHeader, clearSubHeader } from "../assets/js/login.util.js";
import { accessor } from "../assets/js/accessor.js";
import WorkerMenu from "./menu/WorkerMenu.vue";
import ProfileForm from "./form/ProfileForm.vue";
import ChangeForm from "./form/ChangeForm.vue";

export default {
  components: { WorkerMenu, ProfileForm, ChangeForm },
  props: {
    labels: Object,    
    visible: {
      type: [String,Boolean],
      default: false,
    },
  },
  setup(props) {
    let componentVisible = ref(props.visible);
    let workingVisible = ref(props.visible);
    let currentComponent = ref("WorkerMenu");
    return { accessor, componentVisible, workingVisible, currentComponent };
  },
  mounted() {
    this.$nextTick(() => {
        $("#workingframe").on("load",function() { stopWaiting(); refreshScreen(); });
        $(window).on("resize",function() { 
            let wh = $(window).height();
            let kh = 0;
            if($("#kt_header").is(":visible")) {
              kh = $("#kt_header").height();
            }
            let ksh = 0;
            if($("#kt_subheader").is(":visible")) {
              ksh = $("#kt_subheader").height();
            }
            let wfh = (wh-kh-ksh) - 7;
            console.log("workingframe: height",wfh);
            $("#workingframe").height(wfh);
        }).trigger("resize");
        window.startWaiting = startWaiting;
        window.stopWaiting = stopWaiting;
        window.confirmDialogBox = confirmDialogBox;
        window.alertDialog = alertDialog;
        window.confirmDialog = confirmDialog;
        window.alertbox = alertbox;
        window.confirmbox = confirmbox;
        window.alertmsg = alertmsg;
        window.confirmmsg = confirmmsg;
        window.successbox = successbox;
        window.warningbox = warningbox;
        window.displayWorkingFrame = this.displayWorkingFrame;
    });
  },
  methods: {
    reset() {
      console.log("WorkerFrame.vue: reset ...");
      this.componentVisible = false;
      this.workingVisible = false;
      hideWorkSpace();
    },
    setting() {
      this.showWorkerMenu();
    },
    showWorkerMenu() {
      $("#pagecontainer").show();
      this.componentVisible = true;
      this.workingVisible = false;
      this.currentComponent = "WorkerMenu";
      hideWorkSpace();
      showSubHeader("favorite",this.$props.labels.favorite_title);
    },
    hideWorkerMenu() {
      this.componentVisible = false;
    },
    showWorking() { 
      this.workingVisible = true;
      this.componentVisible = false;
      $("#pagecontainer").hide();
      $("#workingframe").show();
    },
    hideWorking() {
      this.workingVisible = false;
      hideWorkSpace();
    },
    showProfile() {
      $("#pagecontainer").show();
      this.workingVisible = false;
      this.componentVisible = true;
      this.currentComponent = "ProfileForm";
      hideWorkSpace();
      showSubHeader("page_profile",this.$props.labels.profile_label);
    },
    showChangePassword() {
      $("#pagecontainer").show();
      this.workingVisible = false;
      this.componentVisible = true;
      this.currentComponent = "ChangeForm";
      hideWorkSpace();
      showSubHeader("page_change",this.$props.labels.changepwd_label);
    },
    componentActivated(name) {
      console.log("component activated: ",name);
      if("profile"==name) this.$refs.viewComponent.display("",false);
      if("changepassword"==name) this.$refs.viewComponent.display("",false);
    },
    processSuccess(action,info) {
      console.log("processSuccess: action",action,", info",info);
      if("profile"==action || "changepassword"==action) {
        this.showWorkerMenu();
      }
    },
    openWorkingFrame(url) {
      clearSubHeader();
      this.workingVisible = true;
      this.componentVisible = false;
      $("#pagecontainer").hide();
      $("#workingframe").attr("src", url).show();
      console.log("openWorkingFrame:",url);
    },
    displayWorkingFrame(url) {
      let canopen = true;
      let curpid = $("#subheader_wrapper").text().trim();
      if(curpid && curpid.length > 0) {
        let screens = getMetaInfo().NOTIFY_UNCHECK_OPEN_SCREENS || ["favorite","worklist"];
        if(!screens.includes(curpid)) {
          canopen = false;
        }
      } 
      if(!canopen && !(String(getMetaInfo().NOTIFY_CHECK_OPEN)=="false")) {
        confirmDialogBox("QS0035",null,"Do you want to open this transaction? <br/>Becareful all current work will be lose",() => {
          this.openWorkingFrame(url);
        });
      } else {
        this.openWorkingFrame(url);
      }
		},
  },
};
</script>
