<template>
  <template v-if="hasProg">
    <li class="kt-menu__item" aria-haspopup="true">
      <a href="javascript:void(0);" @click="menuClick(menu)" class="fa-link-menu-tree kt-menu__link fa-link-menu-item menu-desktop" :data-pid="menu.itemname" :title="menu.itemname" :data-tree="treePath">
        <i class="kt-menu__link-bullet kt-menu__link-bullet--dot"><span></span></i>
        <span class="kt-menu__link-text">{{ getDisplayMenuName(menu) }}</span>
      </a>
    </li>
  </template>
  <template v-else>
    <li class="dropdown kt-menu__item kt-menu__item--submenu" aria-haspopup="true" data-ktmenu-submenu-toggle="hover">
      <a @click="groupClick(menu)" class="kt-menu__link kt-menu__toggle" data-toggle="collapse" :data-target="'#submenu_'+menuId" href="javascript:void(0);">
        <span class="kt-menu__link-icon"><em class="fa fa-list-alt"><span></span></em></span>
        <span class="kt-menu__link-text">{{ menu.text }}</span>
        <i class="kt-menu__ver-arrow la la-angle-right"></i>
      </a>
      <template v-if="hasItems">
        <div class="kt-menu__submenu">
          <span class="kt-menu__arrow"></span>
          <ul :id="'submenu_'+menuId" class="sub-menu panel-collapse collapse" role="menu">    
            <template v-for="(item,index) in menu.items" :key="index">
              <MetroSiderMenuTree :menu="item" :menuId="getMenuId()" @menu-selected="menuSelected" @group-selected="groupSelected" :lang="lang" :treePath="getTreePath(menu)"/>
            </template>    
          </ul>
        </div>
      </template>
    </li>
  </template>
</template>
<script>
import { randomNumber } from "@willsofts/will-app";
import { progmap } from "@/assets/js/sider.js";
export default {
    name: 'MetroSiderMenuTree',
    props: {
      menu: Object,
      menuId: String,
      lang: String,
      treePath: String,
    },
    setup() {
      return { progmap };
    },
    computed: {
      prog() {
        return this.progmap[this.menu.itemname];
      },
      hasItems() {
        return this.menu.items && this.menu.items.length > 0;
      },
      hasProg() {
        return this.menu.itemname && this.menu.itemname.trim().length > 0;
      },
    },
    methods: {
        getMenuId() { return randomNumber()+"_"+new Date().getTime(); },
        getDisplayMenuName(item) {
          return this.$props.lang === 'TH' ? item.caption || item.text : item.text;
        },
        getTreePath(item) {
          return (this.$props.treePath?this.$props.treePath:"") + this.getDisplayMenuName(item) + "#CUT#";
        },
        menuClick(item) {
          let prog = this.progmap[item.itemname];
          let tree = {caption: this.getDisplayMenuName(item), treepath: (this.$props.treePath?this.$props.treePath:"")};
          console.log("menu click",item,"prog",prog,"treepath",tree);
          this.$emit("menu-selected", prog, tree);
        },
        menuSelected(item,tree) {
          if(!tree) tree = {caption: this.getDisplayMenuName(item), treepath: (this.$props.treePath?this.$props.treePath:"")};
          console.log("menu selected",item,", tree",tree);
          this.$emit("menu-selected", item, tree);
        },
        groupClick(item) {
          console.log("group click",item);
          this.$emit('group-selected', item);
        },
        groupSelected(item) {
          console.log("group selected",item);
          this.$emit('group-selected', item);
        },
    },
};
</script>
  