<template>
<ul id="menuitemlist" class="kt-menu__nav nav flex-column sidebar-nav navbar-nav list-sidebar bg-default" role="menu">
    <template v-for="(value,key,counter) in menus.sidemap" :key="key">
        <template v-if="hasMenuTree(value)">
            <li class="kt-menu__item kt-menu__item--submenu" aria-haspopup="true" data-ktmenu-submenu-toggle="hover">
                <a @click="$emit('group-menu-selected', value)" class="kt-menu__link kt-menu__toggle" data-toggle="collapse" :data-target="'#submenu_'+counter" href="javascript:void(0);">
                    <span class="kt-menu__link-icon"><em :class="getDisplayGroupStyle(value)"><span></span></em></span>
                    <span class="kt-menu__link-text">{{ getDisplayGroupName(value) }}</span>
                    <i class="kt-menu__ver-arrow la la-angle-right"></i>
                </a>
                <div class="kt-menu__submenu">
                    <span class="kt-menu__arrow"></span>
                    <ul :id="'submenu_'+counter" class="kt-menu__subnav collapse" role="menu">
                        <template v-for="(menu,index) in value.jsonmenu.items" :key="index">
                            <MetroSiderMenuTree :menu="menu" @menu-selected="menuSelected" :menuId="getMenuId()" :lang="lang" :treePath="getTreePath(value)" />
                        </template>
                    </ul>
                </div>
            </li>
        </template>
        <template v-else>
            <li class="kt-menu__item kt-menu__item--submenu">
                <a @click="$emit('group-menu-selected', value)" class="kt-menu__link kt-menu__toggle" data-toggle="collapse" :data-target="'#submenu_'+counter" href="javascript:void(0);">
                    <span class="kt-menu__link-icon"><em :class="getDisplayGroupStyle(value)"><span></span></em></span>
                    <span class="kt-menu__link-text">{{ getDisplayGroupName(value) }}</span>
                    <i class="kt-menu__ver-arrow la la-angle-right"></i>
                </a>
                <div class="kt-menu__submenu">
                    <span class="kt-menu__arrow"></span>
                    <ul :id="'submenu_'+counter" class="kt-menu__subnav collapse" role="menu">
                        <li v-for="(item,index) in menus.sidelist[key]" :key="index" class="kt-menu__item" aria-haspopup="true">
                            <a href="javascript:void(0);" @click="itemMenuSelected(item)" class="fa-link-menu-tree kt-menu__link fa-link-menu-item menu-desktop" :data-pid="item.programid" :data-url="item.url" :title="item.programid" :data-tree="getTreePath(item)">
                                <i class="kt-menu__link-bullet kt-menu__link-bullet--dot"><span></span></i>
                                <span class="kt-menu__link-text">{{ getDisplayProgramName(item) }}</span>
                            </a>
                        </li>
                    </ul>
                </div>
            </li>
        </template>
    </template>
</ul>
</template>
<script>
import { randomNumber } from "@willsofts/will-app";
import MetroSiderMenuTree from "./MetroSiderMenuTree.vue";
export default {
    components: { MetroSiderMenuTree },
  props: {
    menus: Object,    
    metas: Object,
    lang: String,
  },  
  emits: ["group-menu-selected","item-menu-selected"],
  methods: {
    getDisplayGroupStyle(item) {
        return item.groupstyle && item.groupstyle.trim().length > 0 ? item.groupstyle : "fa fa-tasks";
    },
    getDisplayGroupName(item) {
        return this.$props.lang === 'TH' ? item.nameth : item.nameen;
    },
    getDisplayProgramName(item) {
        return this.$props.lang === 'TH' ? item.prognameth : item.progname;
    },
    getTreePath(item) {
        return this.getDisplayGroupName(item) + "#CUT#";
    },
    itemMenuSelected(item) {
        let tree = {caption: this.getDisplayProgramName(item), treepath: this.getTreePath(item)};
        console.log("menu item selected",item,", tree",tree);
        this.$emit("item-menu-selected", item, tree);
    },
    getMenuId() { return randomNumber()+"_"+new Date().getTime(); },
    isMenuTree() { 
        return this.$props.metas.MENU_TREE; 
    },
    hasMenuTree(item) {
        return this.isMenuTree() && (item.jsonmenu !== undefined);
    },
    getMenuTree(item) {
        let jsonmenu = undefined;
        if(item.menutext && item.menutext.trim().length > 0) {
            try { jsonmenu = JSON.parse(item.menutext); } catch(ex) { console.error(ex); }
        }
        return jsonmenu;
    },
    menuSelected(item,tree) {
        console.log("menu selected",item,", tree",tree);
        this.$emit("item-menu-selected", item, tree);
    },
  },
};
</script>
