<template>
    <div id="kt_header" class="kt-header kt-grid__item kt-header--fixed">
    <div class="kt-header-menu-wrapper" id="kt_header_menu_wrapper">
        <div id="kt_header_menu" class="kt-header-menu kt-header-menu-mobile kt-header-menu--layout-default">
            <ul class="kt-menu__nav"></ul>
        </div>
    </div>
    <div class="kt-header__topbar">
        <div class="kt-header__topbar-item dropdown" v-show="chatEnable">
            <div class="kt-header__topbar-wrapper" data-toggle="dropdown" data-offset="50px,0px" aria-expanded="false" id="chat_select" title="Chat">
                <a href="javascript:void(0)" @click="openChat" id="chatmenuitemlink" class="kt-header__topbar-icon kt-pulse kt-pulse--brand flaticon2-talk"></a>
            </div>
            <div class="dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround dropdown-menu-lg">
            </div>
        </div>
        <div class="kt-header__topbar-item dropdown" id="notificationheader" v-show="notifyEnable">
            <div class="kt-header__topbar-wrapper" data-toggle="dropdown" data-offset="30px,0px" aria-expanded="false" id="noti_select" title="Notification">
                <span class="kt-header__topbar-icon kt-pulse kt-pulse--brand flaticon2-notification"></span>
                <span class="kt-badge kt-badge--danger noti-number" id="noti_number"></span>
            </div>
            <div class="dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround dropdown-menu-lg">
                <form>
                    <MetroNotifyMenu ref="notifyMenu" :visible="notifyVisible" :labels="labels" />
                </form>
            </div>
        </div>        
        <div class="kt-header__topbar-item dropdown">
            <div id="favor_menu_id" class="kt-header__topbar-wrapper" data-toggle="dropdown" data-offset="30px,0px" aria-expanded="true" title="Favorite">
                <span class="kt-header__topbar-icon flaticon2-menu-4"></span>
            </div>
            <div class="dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround dropdown-menu-xl">
                <form>
                    <MetroFavorMenu ref="favorMenu" :visible="favorVisible" :labels="labels" />
                </form>
            </div>
        </div>
        <div class="kt-header__topbar-item kt-header__topbar-item--langs" v-show="languageEnable">
            <div class="kt-header__topbar-wrapper" data-toggle="dropdown" data-offset="10px,0px" title="Language">
                <span class="kt-header__topbar-icon">
                    <img id="languageimage" alt="Language" title="Language" class="img-lang" :class="'img-lang-'+getCurrentLanguage()" />
                </span>
            </div>
            <div class="dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround">
                <ul class="kt-nav kt-margin-t-10 kt-margin-b-10">
                    <li v-for="item in multiLanguages" :key="item.lang" class="li-lang kt-nav__item" :class="isLanguage(item.lang) ? 'kt-nav__item--active' : ''">
                        <a href="javascript:void(0)" @click="changeLanguage(item.lang)" class="kt-nav__link lang-linker">
                            <span class="kt-nav__link-icon"><img class="img-lang" :class="'img-lang-' + item.lang" /></span>
                            <span class="kt-nav__link-text font-size-custom-h-14 lang-word">{{ labels[item.label] }}</span>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="kt-header__topbar-item kt-header__topbar-item--user">
            <div class="kt-header__topbar-wrapper" data-toggle="dropdown" data-offset="0px,0px" title="Menu">
                <div class="kt-header__topbar-user">
                    <span class="kt-header__topbar-welcome kt-hidden-mobile font-size-custom-h2">{{ labels.greet_menu }}</span>
                    <span class="kt-header__topbar-username kt-hidden-mobile font-size-custom-d2" id="accessor_label">{{ accessorFullName }}</span>
                    <img alt="Avatar" src="../../assets/img/avatar.png" id="fs_image_profile_select" />
                </div>
            </div>
            <div class="dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround dropdown-menu-lg">
                <div id="user_card_cover_layer" class="kt-user-card kt-user-card--skin-dark kt-notification-item-padding-x">
                    <div class="kt-user-card__avatar">
                        <img alt="Avatar" src="../../assets/img/avatar.png" id="fs_image_profile" />
                    </div>
                    <div class="kt-user-card__name kt-notification__item-details">
                        <div class="kt-notification__item-title kt-font-bold font-size-custom-h2" id="name_profile">{{ accessorFullName }}</div>
                        <div class="kt-notification__item-time font-size-custom-d2" id="rolename_profile"></div>
                        <div class="kt-notification__item-time font-size-custom-d2" :title="labels.lastaccess_label"><em id="lastclock" class="fa fa-clock-o fas fa-clock"></em><span id="last_access_label">{{ lastAccessTime }}</span></div>
                    </div>
                </div>
                <div class="kt-notification">
                    <a href="javascript:void(0)" @click="menuSelected('changepassword')" class="kt-notification__item" id="accessor_change_link">
                        <div class="kt-notification__item-icon">
                            <i class="fa fa-lock kt-font-danger"></i>
                        </div>
                        <div class="kt-notification__item-details">
                            <div class="kt-notification__item-title kt-font-bold font-size-custom-h-14">
                                {{ labels.changepwd_label }}
                            </div>
                        </div>
                    </a>
                    <a href="javascript:void(0)" @click="menuSelected('profile')" class="kt-notification__item" id="accessor_profile_link">
                        <div class="kt-notification__item-icon">
                            <i class="fa fa-cog "></i>
                        </div>
                        <div class="kt-notification__item-details">
                            <div class="kt-notification__item-title kt-font-bold font-size-custom-h-14">
                                {{ labels.profile_label }}
                            </div>
                        </div>
                    </a>
                    <div class="kt-notification__custom kt-space-between">
                        <a href="javascript:void(0)" @click="menuSelected('logout')" id="accessor_logout_link" class="btn btn-label btn-label-brand btn-sm btn-bold font-size-custom-h-14">
                            {{ labels.logout_label }}
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import $ from "jquery";
import { ref } from 'vue';
import { getDefaultLanguage, setDefaultLanguage, getMultiLanguagesModel, registerNotification } from "@willsofts/will-app";
import { DEFAULT_CONTENT_TYPE, getImgUrl, getApiUrl, getAccessorToken, sendMessageToFrame } from "@willsofts/will-app";
import { accessor } from "../../assets/js/accessor.js";
import MetroFavorMenu from "./MetroFavorMenu.vue";
import MetroNotifyMenu from "./MetroNotifyMenu.vue";

export default {
  components: { MetroFavorMenu, MetroNotifyMenu },    
  props: {
    labels: Object,    
    visible: {
      type: [String,Boolean],
      default: true,
    },
    chatEnable: {
      type: [String,Boolean],
      default: false,
    },
    notifyEnable: {
      type: [String,Boolean],
      default: false,
    },
    languageEnable: {
      type: [String,Boolean],
      default: true,
    },
  },
  emits: ["language-changed","menu-selected"],
  setup() {
    const languageVisible = ref(true);
    const favorVisible = ref(true);
    const notifyVisible = ref(true);
    const multiLanguages = ref(getMultiLanguagesModel());
    return { accessor, languageVisible, favorVisible, notifyVisible, multiLanguages };
  },
  created() {
    registerNotification((action,data) => {
        console.log("registerNotification: "+action,data);
        if("multi-languages"==action) {
            this.multiLanguages = getMultiLanguagesModel(data);
        }
    });
  },
  computed: {
    accessorFullName() { 
        if(this.accessor.info?.displayname && this.accessor.info?.displayname.trim().length > 0) return this.accessor.info?.displayname;
        if(this.accessor.info?.name && this.accessor.info?.surname) return this.accessor.info?.name+" "+this.accessor.info?.surname;
        return this.accessor.info?.username ? this.accessor.info?.username : ""; 
    },
    lastAccessTime() { return this.accessor.info?.accessdate && this.accessor.info?.accesstime ? this.accessor.info?.accessdate+" "+this.accessor.info?.accesstime : ""; },
  },
  methods: {
    isLanguage(lang) {
        return lang == accessor.lang || lang == getDefaultLanguage();
    },
    getCurrentLanguage() {
        return accessor.lang || getDefaultLanguage();
    },
    reset() {
        console.log("HeaderBar: reset ...");
        this.$refs.favorMenu.reset();
        this.clearAvatar();
    },
    menuSelected(menu) {
        this.$emit("menu-selected", menu);
	},
    changeLanguage(lang) {
        console.log("change language: ",lang);
        setDefaultLanguage(lang);
        this.accessor.lang = lang;
        this.$emit('language-changed', lang);
        sendMessageToFrame({type:"language",language:lang});
    },
    setting(callback) {
        console.log("HeaderBar: setting, accessor",this.accessor,"callback",callback);
        let avatar = this.accessor.info?.avatar;
        if(avatar && avatar.trim().length > 0) {
            this.showAvatar(avatar);
        } else {
            this.fetchAvatar(this.accessor.info?.userid,this.accessor.info?.photoimage);
        }
        this.$refs.favorMenu.setting();
    },
    showLanguage() { this.languageVisible = true; },
    hideLanguage() { this.languageVisible = false; },
    showFavorite() { this.favorVisible = true; },
    hideFavorite() { this.favorVisible = false; },
    displaySideBar() { 
        console.log("displaySideBar ...");
    },    
    collapseSideBar() { 
        console.log("collapseSideBar ...");
    },
    openChat() {
        this.$emit('open-chat');
    },
    clearAvatar() {
        this.showAvatar(getImgUrl()+"/img/avatar.png");
    },
    showAvatar(avatar) {
        if(!avatar || avatar.trim().length==0) return;
        let data = avatar.indexOf("data:") >= 0;
        avatar = data ? avatar : getImgUrl() + avatar;
        $("#fs_image_profile_select").attr("src",avatar);
        $("#fs_image_profile").attr("src",avatar);
    },
    fetchAvatar(userid,photoimage) {
        let authtoken = getAccessorToken();
        console.log("fetchAvatar: userid="+userid);
        $.ajax({ 
            url : getApiUrl()+"/api/avatar/image", 
            data: JSON.stringify({ userid: userid, photoimage: photoimage }), 
            headers : { "authtoken": authtoken }, 
            type : "POST",
            dataType: "json",
            contentType: DEFAULT_CONTENT_TYPE,
            success: (data) => { 
                this.showAvatar(data?.avatar);
            }
        });
    },
  },
};
</script>
